<template>
  <b-card no-body>
    <!-- <DialogHeader name="fontawesome"></DialogHeader> -->
    <b-card-header>
      <b-row class="p-2 text-white">
        <b-col class="col-6">
          <span class="mb-3 font-weight-bold" style="font-size:1.2em;">21대 국회 의석수 계산기</span>
        </b-col>
        <b-col class="col-6 text-right">
          <span class="mb-3">최종업데이트 : 2020-04-07 11:35</span>
        </b-col>
      </b-row>
      <b-row class="p-2">
        <b-col class="col-1">
          지역구 예상 분석
        </b-col>
        <b-col class="col-11">
          <!-- <b-button
            class="col-poll mx-2 mt-1 mb-3"
            size="sm"
            variant="info"
            @click.prevent.stop="selectLocalSeatPoll(finalLocalSeat, finalLocalSeatPoll)">
            지역구 최종 득표율
          </b-button> -->

          <b-button
            id="summarized-local-seat-tip"
            class="col-poll mx-2 mt-1 mb-3"
            size="sm"
            variant="info"
            @click.prevent.stop="selectLocalSeatPoll(summarizedLocalSeat, summarizedLocalSeatPoll)">
            지역구 여론조사 종합
          </b-button>
          <b-tooltip target="summarized-local-seat-tip">여론조사를 기반으로 산정한 데이터입니다. 여론조사가 없는 지역구는 여론조사가 진행된 지역구의 정당득표 비율로 다시 계산되었습니다.</b-tooltip>

          <b-button size="sm"
            class="col-poll mx-2 mt-1 mb-3"
            @click.prevent.stop="selectLocalSeatSample(localSeatVote20)"
          >
            20대 의석수 대비
          </b-button>

          <b-button size="sm"
            class="col-poll mx-2 mt-1 mb-3"
            @click.prevent.stop="selectLocalSeatSample(localSeatVote21Sample3)"
          >
            선거위키(2019-03-31)
            <b-badge pill variant="primary" class="col-label"
              @click.stop.prevent="goLink('http://blog.naver.com/PostView.nhn?blogId=electionwiki&logNo=221882112337')">연결
            </b-badge>
          </b-button>

          <b-button size="sm"
            class="col-poll mx-2 mt-1 mb-3"
            @click.prevent.stop="selectLocalSeatSample(localSeatVote21Sample1)"
          >
            선거위키(2019-03-17)
            <b-badge pill variant="primary" class="col-label"
              @click.stop.prevent="goLink('http://blog.naver.com/PostView.nhn?blogId=electionwiki&logNo=221858590924')">연결
            </b-badge>
          </b-button>
          <b-button size="sm"
            class="col-poll mx-2 mt-1 mb-3"
            @click.prevent.stop="selectLocalSeatSample(localSeatVote21Sample2)"
          >
            한국경제(2019-03-11)
            <b-badge pill variant="primary" class="col-label"
              @click.stop.prevent="goLink('https://www.hankyung.com/politics/article/202003112169i')">연결
            </b-badge>
          </b-button>
        </b-col>
      </b-row>

      <b-row class="p-1">
        <b-col class="col-1">
          
        </b-col>
        <b-col class="col-11">
          <template v-for="(lspl, index) in local_seat_poll_link">
          <b-button
            :key="index"
            class="col-poll mx-2 mt-1 mb-3"
            size="sm"
            @click.prevent.stop="goLink(lspl.link)">
            {{ lspl.name }}<br />
            <small>{{ lspl.update }}</small>
          </b-button>
          </template>
        </b-col>
      </b-row>
      
      <b-row class="p-2">
        <b-col class="col-1">
          비례의석 여론조사
        </b-col>
        <b-col class="col-11">
          <!-- <b-button
            class="col-poll mx-2 mt-1 mb-3"
            size="sm"
            variant="info"
            @click.prevent.stop="selectProportionalSeatPoll(finalProportionalSeatPoll)">
            비례대표 득표율<br />
            <small>4월 15일 투표일</small>
          </b-button> -->

          <b-button
            class="col-poll mx-2 mt-1 mb-3"
            size="sm"
            variant="info"
            @click.prevent.stop="selectProportionalSeatPoll(summarizedProportionalSeatPoll)">
            비례대표 여론조사 종합<br />
            <small>3월 13일 ~ 현재</small>
          </b-button>
          <b-badge pill variant="primary" class="col-badge">{{ summarizedProportionalSeatPoll.respondent | digit }}명</b-badge>

          <template v-for="(poll, index) in proportionalSeatPollList">
          <b-button
            :key="index"
            class="col-poll mx-2 mt-1 mb-3"
            size="sm"
            @click.prevent.stop="selectProportionalSeatPoll(poll)">
            {{ poll.requester }}, {{ poll.research }}<br />
            <small>{{ poll.period }}</small>
            <b-badge pill variant="primary" class="col-badge"> {{ poll.respondent | digit }}명</b-badge>
          </b-button>
          </template>
        </b-col>
      </b-row>
      <b-row class="p-1">
        <b-col class="col-12 text-right">
          <b-button size="sm" class="mx-1" @click.stop.prevent="goLink('https://namu.wiki/w/%EB%B6%84%EB%A5%98:%EB%8C%80%ED%95%9C%EB%AF%BC%EA%B5%AD%20%EA%B5%AD%ED%9A%8C%EC%9D%98%EC%9B%90%20%EC%84%A0%EA%B1%B0')">지역구 여론조사 출처 : 나무위키 </b-button>
          <b-button size="sm" class="mx-1" @click.stop.prevent="goLink('https://ko.wikipedia.org/wiki/%EB%8C%80%ED%95%9C%EB%AF%BC%EA%B5%AD_%EC%A0%9C21%EB%8C%80_%EA%B5%AD%ED%9A%8C%EC%9D%98%EC%9B%90_%EC%84%A0%EA%B1%B0_%EC%97%AC%EB%A1%A0_%EC%A1%B0%EC%82%AC#5%EC%B0%A8_%EC%A0%95%EA%B3%84_%EA%B0%9C%ED%8E%B8_%EC%9D%B4%ED%9B%84_2')">비례 여론조사 출처 : 위키피디아(5차 정계 개편 이후) </b-button>
          <b-button size="sm" class="mx-1" @click.stop.prevent="goLink('http://www.ohmynews.com/NWS_Web/View/at_pg_w.aspx?CNTN_CD=A0002623791')">지도 정보 : 오마이뉴스</b-button>
          <!-- <b-button size="sm" class="mx-1" @click.stop.prevent="goLink('https://github.com/OhmyNews/2020_21_elec_map/blob/master/2020_21_elec_253_simple.json')">지도 정보 : GeoJson</b-button> -->
        </b-col>
      </b-row>
    </b-card-header>
    <b-card-body>
      <b-row>
        <b-col class="col-12 p-5">
          <b-container fluid class="p-0">
          <b-row class="text-center py-1" style="font-size:1.1em;border-bottom:1px dashed;">
            <b-col :class="clazz(1)">정당</b-col>
            <b-col :class="clazz(2)">지역구 의석수</b-col>
            <b-col :class="clazz(2)">정당 지지율</b-col>
            <b-col :class="clazz(3)">환산 지지율</b-col>
            <b-col :class="clazz(3)">준연동형 비례</b-col>
            <b-col :class="clazz(3)">병립형 비례</b-col>
            <b-col :class="clazz(4, ['py-1', 'col-poll'])">합계
              <b-badge pill class="col-label">비율</b-badge>
            </b-col>
          </b-row>

          <template v-for="(key, index) in partyKey">
            <b-row :key="key" class="py-2 hover-row" style="border-bottom:1px dashed;">
              <b-col :class="clazz(1, ['py-1', 'col-poll'])">
                <template v-if="partyList[key].no">
                  {{ partyList[key].no }}.
                </template>
                {{ partyList[key].name }}
                <b-badge pill class="col-group" :style="{backgroundColor: partyGroup[partyList[key].group].color}">{{ partyGroup[partyList[key].group].name }}</b-badge>
              </b-col>

              <template v-if="!partyList[key].localLock">
                <b-col :class="clazz(2)">
                  <b-form-input
                    type="number"
                    v-model="input_party[index].localseat"
                    @keyup="calculate"
                    @change="calculate"
                    :disabled="partyList[key].localLock"
                    class="text-right"></b-form-input>
                </b-col>
              </template>
              <template v-else-if="!['part90', 'noparty', 'undecided'].includes(key)">
                <b-col :class="clazz(2, ['text-center', 'py-1'])">지역후보없음</b-col>
              </template>
              <template v-else>
                <b-col :class="clazz(2)"></b-col>
              </template>

              <template v-if="!partyList[key].ratioLock">
                <b-col :class="clazz(2)">
                  <b-form-input
                    type="number"
                    v-model="input_party[index].ratio"
                    @keyup="calculate"
                    @change="calculate"
                    :disabled="partyList[key].ratioLock"
                    class="text-right"></b-form-input>
                </b-col>
              </template>
              <template v-else-if="!['part90', 'noparty', 'undecided'].includes(key)">
                <b-col :class="clazz(2, ['text-center', 'py-1'])">비례후보없음</b-col>
              </template>
              <template v-else>
                <b-col :class="clazz(2)"></b-col>
              </template>

              <b-col :class="clazz(3, ['text-right', 'py-1', 'meaning'])">
                <template v-if="!['part90', 'noparty', 'undecided'].includes(key) && ratio100[key] > 0">
                  <strong :style="{color:partyList[key].color}">{{ ratio100[key] | ratio }}</strong>
                </template>
                <template v-else>
                  -
                </template>
              </b-col>

              <b-col :class="clazz(3, ['text-right', 'py-1', 'meaning'])">
                <template v-if="!['part90', 'noparty', 'undecided'].includes(key) && proportional_seat30[key] > 0">
                  <strong :style="{color:partyList[key].color}">{{ proportional_seat30[key] | digit }}</strong>
                </template>
                <template v-else>
                  -
                </template>
              </b-col>
              <b-col :class="clazz(3, ['text-right', 'py-1', 'meaning'])">
                <template v-if="!['part90', 'noparty', 'undecided'].includes(key) && proportional_seat17[key] > 0">
                  <strong :style="{color:partyList[key].color}">{{ proportional_seat17[key] | digit }}</strong>
                </template>
                <template v-else>
                  -
                </template>
              </b-col>
              <b-col :class="clazz(4, ['text-right', 'py-1', 'col-poll', 'meaning'])">
                <template v-if="!['part90', 'undecided'].includes(key) && seatRatio(index, key) > 0">
                  <strong :style="{color:partyList[key].color}">
                    <span class="mr-4">{{ (readLocalSeat(index) + (proportional_seat30[key] || 0) + (proportional_seat17[key] || 0)) | digit }}</span>
                  </strong>
                  <b-badge
                    pill
                    class="col-percent"
                    v-if="seatRatio(index, key) > 0"
                    :style="{backgroundColor: partyList[key].color}"
                  >
                    {{ seatRatio(index, key) | ratio }}
                  </b-badge>
                </template>
                <template v-else>
                  <span class="mr-4">-</span>
                </template>
              </b-col>
            </b-row>
          </template>
          <b-row class="mt-1">
            <b-col :class="clazz(1)">합계<small>(무당층 제외)</small></b-col>
            <b-col :class="clazz(2)">
                <b-form-input type="number"
                  v-model="input_localseat_total"
                  :disabled="true"
                  aria-describedby="validate-feedback-localseat-total"
                  class="text-right"></b-form-input>
                <!-- {{ getLocalSeatTotal | digit }} -->
            </b-col>
            <b-col :class="clazz(2)">
                <b-form-input type="number" v-model="input_ratio_total" :disabled="true" class="text-right"></b-form-input>
                <!-- {{ getRatioTotal | ratio }} -->
            </b-col>
            <b-col :class="clazz(3, ['text-right'])">
                <!-- <b-form-input type="number" v-model="input_ratio100_total" :disabled="true" class="text-right"></b-form-input> -->
                <!-- {{ input_ratio100_total | ratio }} -->
            </b-col>
            <b-col :class="clazz(3, ['text-right'])">
                <!-- <b-form-input type="number" v-model="input_proportional_seat30_total" :disabled="true" class="text-right"></b-form-input> -->
                {{ input_proportional_seat30_total | digit }}
            </b-col>
            <b-col :class="clazz(3, ['text-right'])">
                <!-- <b-form-input type="number" v-model="input_proportional_seat17_total" :disabled="true" class="text-right"></b-form-input> -->
                {{ input_proportional_seat17_total | digit }}
            </b-col>
            <b-col :class="clazz(4)">
                <b-form-input type="number" v-model="input_seat_total" :disabled="true" class="text-right"></b-form-input>
                <!-- {{ getSeatTotal | digit }} -->
            </b-col>
          </b-row>
          </b-container>
        </b-col>
      </b-row>
      <b-row class="mt-2">
        <b-col class="col-12 col-lg-4">
          <b-row class="mb-5">
            <b-col class="col-6 col-lg-12">
              <vote21-chart
                ref="party_group_chart"
                :chart-data="groupData"
                :options="chartOptions"
                height="400"
                >
              </vote21-chart>
            </b-col>
            <b-col class="col-6 col-lg-12">
              <vote21-chart
                ref="party_seat_chart"
                :chart-data="chartData"
                :options="chartOptions"
                height="400"
                >
              </vote21-chart>
            </b-col>
          </b-row>
        </b-col>
        <b-col class="col-12 col-lg-8">
          <div id="map-wrapper" class="map-wrapper">
            <div v-if="province" class="province-title text-left">
              <h3>{{province.SGG_2}} : {{ province.party }}, {{ province.candidate }}</h3>
              <!-- <div>
                <span v-html="province.metadata[1]"></span>
              </div> -->
            </div>
            <div v-if="currentProvince" class="province-info">
              <h3 class="text-center">
                {{currentProvince.SGG_2}}
                : {{ findPartyName( currentProvince.SGG_Code )}}
                , {{ findCandidate( currentProvince.SGG_Code )}}
              </h3>
              <ul class="text-left">
                <li>
                  <span v-html="currentProvince.metadata[1]"></span>
                </li>
                <!--
                <li>정당: {{findPartyName(currentProvince.SGG_Code)}}</li>
                <li>SGG_Code: {{currentProvince.SGG_Code}}</li>
                <li>SGG_1: {{currentProvince.SGG_1}}</li>
                <li>SGG_2: {{currentProvince.SGG_2}}</li>
                <li>SGG_2: {{currentProvince.SGG_3}}</li>
                -->
                <li> 후보자 :
                  <ul>
                    <template v-for="candidate in localSeatCandidate[currentProvince.SGG_Code]">
                      <li :key="candidate.no">기호 {{ candidate.no }} 번, {{ candidate.partyName }}, {{ candidate.name }}</li>
                    </template>
                  </ul>
                </li>
                <li> 여론조사
                  <ul>
                    <template v-for="(poll, index) in localSeatPolls[currentProvince.SGG_Code]">
                      <b-row :key="index" class="mt-1">
                        <b-col class="col-5 text-white">
                          {{ poll.research }}<br />
                        </b-col>
                        <b-col class="col-7 text-right">
                          <small>
                            응답자 : {{ poll.period }}, {{ poll.respondent | digit }}명
                            , <a href="#" @click.stop.prevent="goLink(poll.link)">출처</a>
                          </small>
                        </b-col>
                        <b-col class="col-12 text-right">
                          <b-progress class="mt-2" max="100" show-value>
                            <template v-for="item in poll.data">
                              <b-progress-bar :key="item.name" :value="item.ratio" :precision="2" :style="{backgroundColor:partyList[item.party].color, color:'#ffffff'}">{{item.name}}({{item.ratio}}%)</b-progress-bar>
                            </template>
                          </b-progress>
                          <small>
                          </small>
                        </b-col>
                      </b-row>
                    </template>
                  </ul>
                </li>
              </ul>
            </div>
                
            <div class="province-summary text-left">
              <h4 class="text-center">지역구 득표수</h4>
              <ul>
                <template v-for="(value, key) in summarizedLocalSeat">
                  <li :key="key">{{ partyList[key].name }} : {{value | digit }}석</li>
                </template>
              </ul>
            </div>
          </div>
        </b-col>
      </b-row>
      

      <template v-for="(localSeatPoll, key) in localSeatPolls">
        <b-row :key="key" class="m-2 mt-3 pt-2 poll-hover-row">
          <b-col class="col-12 text-white">
            <strong>{{ localSeatCodes[key].SGG_2 }}</strong>
            <span
              class="mx-2 p-1"
              :style="{backgroundColor:findPartyColor(key)}"
              v-if="findPartyName(key)"
            >
              {{ findPartyName( key )}}, {{ findCandidate( key )}}
            </span>
            <b-row class="mb-3" v-if="key in summarizedLocalSeatPoll">
              <b-col class="col-12 pl-5">
                <b-row class="mt-1">
                  <b-col class="col-12 text-white">
                    여론조사 종합
                  </b-col>
                </b-row>
                <b-row>
                  <b-col class="col-12 text-right">
                    <b-progress class="mt-2" max="100" show-value>
                      <template v-for="(item, index) in summarizedLocalSeatPoll[key]">
                        <b-progress-bar
                          :key="index + '-' + item.name"
                          :value="item.ratio"
                          :precision="2"
                          :style="{backgroundColor:partyList[item.party].color, color:'#ffffff'}"
                        >
                          {{item.name}}({{item.ratio}}%)
                        </b-progress-bar>
                      </template>
                    </b-progress>
                    <small>
                    </small>
                  </b-col>
                </b-row>
              </b-col>
            </b-row>
            <b-row>
              <template v-for="(poll, index) in localSeatPoll">
                <b-col :key="index" class="col-12 col-lg-6 col-xl-4 pl-5">
                  <b-row class="mt-1">
                    <b-col class="col-12 text-white">
                      <template v-if="poll.requester && poll.research">
                        {{ poll.requester }}, {{ poll.research }}
                      </template>
                      <template v-else>
                        {{ poll.requester }} {{ poll.research }}
                      </template>
                    </b-col>
                    <!-- <b-col class="col-12 col-md-7 text-right">
                    </b-col> -->
                    <b-col class="col-12 text-right">
                      <b-progress class="mt-2" max="100" show-value>
                        <template v-for="item in poll.data">
                          <b-progress-bar
                            :key="index + '-' + item.name"
                            :value="item.ratio"
                            :precision="2"
                            :style="{backgroundColor:partyList[item.party].color, color:'#ffffff'}"
                          >
                            {{item.name}}({{item.ratio}}%)
                          </b-progress-bar>
                        </template>
                      </b-progress>
                      <small>
                      </small>
                    </b-col>
                  </b-row>
                  <b-row class="mb-2">
                    <b-col class="col-12 pl-3">
                      <small>
                        여론조사기간 : {{ poll.period }}
                        <!-- , {{ poll.respondent | digit }}명 -->
                        , <a href="#" @click.stop.prevent="goLink(poll.link)">출처</a>
                      </small>
                      <br />
                      <small>{{ poll.desc }}</small>
                    </b-col>
                  </b-row>
                </b-col>
              </template>
            </b-row>
          </b-col>
        </b-row>
      </template>

    </b-card-body>
    <b-card-footer>

    </b-card-footer>
  </b-card>
</template>



<script>
import Vue from 'vue'

const __TOTAL_SEAT = 300;
const __NO_RATIO_SEAT = 253;
const __RATIO_SEAT_CAP = 30;
const __RATIO_SEAT = __TOTAL_SEAT - __NO_RATIO_SEAT;
const __RATIO_SEAT_NO_CAP = __RATIO_SEAT - __RATIO_SEAT_CAP;

// const polls = require('./polls.json');
// console.log("polls : " + JSON.stringify(polls));

// https://ko.wikipedia.org/wiki/%EB%8C%80%ED%95%9C%EB%AF%BC%EA%B5%AD_%EC%A0%9C21%EB%8C%80_%EA%B5%AD%ED%9A%8C%EC%9D%98%EC%9B%90_%EC%84%A0%EA%B1%B0_%EC%97%AC%EB%A1%A0_%EC%A1%B0%EC%82%AC#5%EC%B0%A8_%EC%A0%95%EA%B3%84_%EA%B0%9C%ED%8E%B8_%EC%9D%B4%ED%9B%84_2
const proportionalSeatPolls = require('./proportional.poll.json');
const localSeatPolls = require('./localseat.poll').default;
const localSeatCodes = require('./localseat.code.json');
const localSeatCandidate = require('./localseat.candidate').default;

// import localSeatPolls from './localseat.poll'
// console.log(localSeatPolls);

// http://www.ohmynews.com/NWS_Web/View/at_pg_w.aspx?CNTN_CD=A0002623791
// https://github.com/OhmyNews/2020_21_elec_map/blob/master/2020_21_elec_253_simple.json
// https://namu.wiki/w/%EC%A0%9C21%EB%8C%80%20%EA%B5%AD%ED%9A%8C%EC%9D%98%EC%9B%90%20%EC%84%A0%EA%B1%B0/%EC%97%AC%EB%A1%A0%EC%A1%B0%EC%82%AC
// https://namu.wiki/w/%EC%A0%9C21%EB%8C%80%20%EA%B5%AD%ED%9A%8C%EC%9D%98%EC%9B%90%20%EC%84%A0%EA%B1%B0/%ED%9B%84%EB%B3%B4%EC%9E%90

// import { Vote21Chart } from './Vote21Chart'
const Vote21Chart = require('./Vote21Chart').default;

import * as d3 from 'd3'

export default {
  components: {
    // DialogHeader,
    "vote21-chart" : Vote21Chart
  },
  data() {
    return {
