      } else if(type === 2) {
        ret.push(...["col-6", "col-sm-3", "col-md-3", "col-xl-2"]);
      } else if(type === 3) {
        ret.push(...["col-6", "col-sm-4", "col-md-3", "col-xl-1"]);
      } else if(type === 4) {
        ret.push(...["col-2", "col-sm-4", "col-md-5", "col-xl-2"]);
      }
      if(extra) {
        ret.push(...extra);
      }
