            // Object.keys(pollSummary2).forEach(key => {
            //   const {party, name, ratio, respondent} = pollSummary2[key];
            //   const newratio = ratio / respondent;

            //   summary2[localcode].push({party, name, ratio : newratio})
            // });

          });
