      //     }
      //     total++;
      //   }
      // });
      // summarizedLocalSeat2["undecided"] = __NO_RATIO_SEAT - total;
      // console.log("summarizedLocalSeat2 : ", summarizedLocalSeat2);
      // this.summarizedLocalSeatWithRespondent = summarizedLocalSeat2;
      // this.summarizedLocalSeatPollWithRespondent = summary2;
    },

    summaryProportionalSeatPoll() {
      // "requester": "SBS",
      // "research": "입소스",
      // "period": "3월 28일 ~ 3월 29일",
      // "respondent": 1001,
      // "minsaengdang": 0.60,
      // "futurekorea": 24.40,
      // "platformparty": 20.40,
      // "justice21": 13.50,
      // "ourrepublican": 0.80,
      // "minjung": 0.70,
      // "koreaeconomy": 0.40,
      // "people21": 5.30,
      // "proparknew": 1.00,
      // "openminjoo": 11.90,
      // "undecided": 20.90
      const sum = {};
      proportionalSeatPolls.forEach(item => {
          const respondent = item.respondent;   // 응답자
          Object.keys(item).forEach(key => {
            if(key !== "requester" && key !== "research" && key !== "period" && key !== "respondent") {
              const ratio = item[key];
              if(ratio >= 0) {
                const value = ratio * respondent;
                if(key in sum) {
                  sum[key].ratio += value;
                  sum[key].respondent += respondent;
                } else {
                  sum[key] = {ratio : value, respondent};
                }
              }
            }
          })
      });
      
      const summary = {};
      // console.log("proportionalSeatPolls Sum : ", sum);

      let respondent = 0;
      Object.keys(sum).forEach(key => {
        summary[key] = sum[key].ratio / sum[key].respondent;
        if(key === "undecided") {
          respondent = sum[key].respondent;
        }
      });

      // console.log("proportionalSeatPolls Summary : ", summary);

      this.summarizedProportionalSeatPoll = summary;
      this.summarizedProportionalSeatPoll.respondent = respondent;
    },

    validateState(name) {
      console.log(JSON.stringify(name));
      const { $dirty, $error } = this.$v.localseats[name];
      return $dirty ? !$error : null;
    },
    readLocalSeat(index) {
      if(this.input_party[index] && this.input_party[index].localseat) {
        return parseFloat(this.input_party[index].localseat);
      }
      return 0;
    },
    seatRatio(index, key) {
      return (parseFloat(this.input_party[index].localseat) + (this.proportional_seat30[key] || 0) + (this.proportional_seat17[key] || 0))/300 * 100;
    },
    redistribute(amount, redistributeRatio) {
        const ret = {};
        let ratioSum = 0;
        Object.keys(redistributeRatio).forEach(key => {
          const data = redistributeRatio[key];
          if(data) {
            // console.log(key + " => " + data);
            ratioSum += data;
          }
        });
        // console.log("재배분 비율합계 : " + ratioSum);


        // 재배분 1단계
        let step1sum = 0;
        let remains = {};
        const share_party_list = [];
        console.log("1차 배분");
        Object.keys(redistributeRatio).forEach(key => {
          const data = redistributeRatio[key];
          if(data) {
            const redata = data * amount / ratioSum;
            const relocalseat = Math.floor(redata);

            // remains[key] = redata - relocalseat;
            // console.log("\t", this.partyList[key].name, relocalseat + " => " + remains[key]);

            console.log("\t", this.partyList[key].name, relocalseat);
            share_party_list.push({key:key, value: redata - relocalseat});

            ret[key] = relocalseat;
            step1sum += relocalseat;
          } else {
            ret[key] = 0;
          }
        });

        const rest_localseat = amount-step1sum;
        console.log("1차 배분 합계 : " + step1sum);
        console.log("1차 배분 후 남은 의석수 : " + rest_localseat);

        // // 재배분 1단계
        // const remains2 = this.firstCome(remains, rest_localseat);
        // console.log("2차 배분 정당 높은 순 : " + remains2);
        // remains2.forEach(key => {
        //   ret[key] += 1;
        // });

        const filled = this.fillRest(share_party_list, rest_localseat);
        Object.keys(filled).forEach(key => {
          ret[key] += filled[key];
        });

        return ret;

    },
    fillRest(share_party_list, amount) {
      share_party_list.sort(function(a, b) {
        // 내림차순
        return b.value - a.value;
        // 44, 25, 21, 13
      });

      console.log("소수점 배분 순서");
      share_party_list.forEach(party => {
        console.log("\t", this.partyList[party.key].name, party.value);
      });

      var share_party_count = share_party_list.length;
      console.log("비례배분 정당 수:", share_party_count);

      const second_share = {};
      for (var i = 1; i < amount + 1; i++) {
        let temp = i % share_party_count;
        temp = (temp == 0) ? share_party_count : temp;

        const party_index = temp - 1;
        const party = share_party_list[party_index].key;

        // console.log("비례배분 : ", party_index, JSON.stringify(share_party_list[party_index]))
        if(second_share[party]) {
          second_share[party]++;
        } else {
          second_share[party] = 1;
        }
        //console.log(temp, share_party_list[party_key].party);
      }
      // console.log("비례배분 : " + JSON.stringify(second_share))
      return second_share;
    },
    firstCome(list, amount) {
      if(amount <= 0) return [];

      let firstParty = "";
      let partyValue = 0;

      Object.keys(list).forEach(key=> {
        if(partyValue <= list[key]){
          firstParty = key;
          partyValue = list[key];
        }
      });

      const ret = [];
      ret.push(firstParty);
      if(amount > 1) {
        delete list[firstParty];
        const nextParty = this.firstCome(list, amount - 1);
        ret.push(...nextParty);
      }

      return ret;
    },

    selectProportionalSeatPoll(poll) {
      // summarizedProportionalSeatPoll
      // selectedProportionalSeatPoll
      // console.log("selectProportionalSeatPoll", poll);
      this.selectedProportionalSeatPoll = poll;

      this.partyKey.forEach((key, index) => {
        // console.log(key, index);
        if(poll.hasOwnProperty(key) && poll[key] > 0) {
          const ratio = parseFloat(poll[key]) || 0;
          this.input_party[index].ratio = ratio.toFixed(2);
        } else {
          this.input_party[index].ratio = 0;
        }
      });

      this.calculate();
    },
    
    selectLocalSeatSample(sample) {
      console.log("지역구의석 재배분", sample);

      const localSeatRatio = {};
      Object.keys(this.partyList).forEach(key => {
        const party = this.partyList[key];
        const data = sample[key];

        if(data) {
          console.log("\t", party.name, data);
