      } else {
        //first_share_total 30보다 작은경우

        console.log("1차 배분 30 이하인 경우");
        var rest_localseat = __RATIO_SEAT_CAP - first_share_total; // 1차 배분후 남은 의석 - 소수점이하 숫자기준 배분함
        //ex_list2  사용해서 나머지 배분

        console.log("1차 배분 후 남은 의석수: ", rest_localseat);
        //1차배분후 남은 의석 배분 ex) rest_localseat =6

        const second_share = this.redistribute(rest_localseat, this.ratio100);

        // console.log("1차 배분 최종(정수+소수배분):", JSON.stringify(first_share));
        // console.log("2차 배분 최종(정수+소수배분):", JSON.stringify(second_share));
        
        console.log("준연동 비례배분");
        let seat30sum = 0;
        this.partyKey.forEach((key, index) => {
          const party = this.partyList[key];
          this.proportional_seat30[key] = (first_share[key] || 0) + (second_share[key] || 0);
          seat30sum += this.proportional_seat30[key];
          console.log("\t", this.partyList[key].name, this.proportional_seat30[key] || 0);
