      const party_group_dataset = {};
      this.partyKey.forEach((key, index) => {
        const party = this.partyList[key];
        let num = (this.readLocalSeat(index) + (this.proportional_seat30[key] || 0) + (this.proportional_seat17[key] || 0));
        seatTotal += num || 0;

        // console.log("\t", party.name, this.readLocalSeat(index), this.proportional_seat30[key], this.proportional_seat17[key]);
