    selectProvince(province) {
      // console.log(province);
      if(province) {
        const party = this.findPartyName(province.SGG_Code);
        const candidate = this.findCandidate(province.SGG_Code);
        province.party = party;
        province.candidate = candidate;
      }
      this.province = province;
    },

    openInfo(province) {
      // console.log(province);
      this.currentProvince = province;
    },
    closeInfo() {
      this.currentProvince = undefined;
    },

    findCandidate(code) {
      // const code = d.properties.SGG_Code;
      const poll = this.selectedLocalSeatPoll["" + code];
      if(poll && Array.isArray(poll) && poll.length > 0) {
        // console.log(poll);
        let candidate = "";
        let maxRatio = 0;
        
        poll.forEach(item => {
          if(maxRatio < parseFloat(item.ratio)) {
            maxRatio = parseFloat(item.ratio);
            candidate = item.name;
            // console.log(key);
          }
        });
        
        return candidate;
      }
      return "";
    },
    findPartyName(code) {
      const party = this.findParty(code);
      if(party) {
        return party.name;
      }
      return "";
    },
    findPartyColor(code) {
      const party = this.findParty(code);
      if(party) {
        return party.color;
      }
      return null;
    },
    
    findParty(code) {
      // const code = d.properties.SGG_Code;
      const poll = this.summarizedLocalSeatPoll["" + code];
      if(poll && Array.isArray(poll) && poll.length > 0) {
        // console.log(poll);
        let party = "";
        let maxRatio = 0;
        
        poll.forEach(item => {
          if(maxRatio < parseFloat(item.ratio)) {
            maxRatio = parseFloat(item.ratio);
            party = item.party;
            // console.log(key);
          }
        });
        
        return this.partyList[party];
      }
      return null;
    },

    // https://codepen.io/lantoine/pen/ExxEGxg
    // https://m.blog.naver.com/PostView.nhn?blogId=jhc9639&logNo=221195987416&proxyReferer=https%3A%2F%2Fwww.google.com%2F
    drawMap() {
      const localseats = require('./localseat.map.json');
      // const koreaMap = require('../map/skorea_municipalities_topo_simple.json');
      // const geojson = localseats.feature(localseats);
      const geojson = localseats;
      const center = d3.geoCentroid(geojson);

      let centered = undefined;

      const divWidth = document.getElementById("map-wrapper").clientWidth;
      const width = (divWidth < 1000) ? divWidth * 0.9 : 1000;
      const height = width * 1;
      const svg = d3
      // .select('.d3')
        .select('.map-wrapper')
        .append('svg')
        .attr('width', width)
        .attr('height', height);
      // const map = svg.append('g');

      const background = svg.append('rect')
        .attr('class', 'background')
        .attr('width', width)
        .attr('height', height)

      const g = svg.append('g');
      const effectLayer = g.append('g').classed('effect-layer', true);
      const mapLayer = g.append('g').classed('map-layer', true);

      let projection = d3.geoMercator()
        .scale(1)
        .translate([0, 0]); 

      const path = d3.geoPath().projection(projection);
      const bounds = path.bounds(geojson);
      const widthScale = (bounds[1][0] - bounds[0][0]) / width; 
      const heightScale = (bounds[1][1] - bounds[0][1]) / height; 
      const scale = 0.95 / Math.max(widthScale, heightScale);
      const xoffset = width/2 - scale * (bounds[1][0] + bounds[0][0]) /2 + 0; 
      const yoffset = height/2 - scale * (bounds[1][1] + bounds[0][1])/2 + 0; 
      const offset = [xoffset, yoffset];
      projection.scale(scale).translate(offset);

      const _this = this;
      function clicked(d) {
        var x, y, k;

        // Compute centroid of the selected path
        if (d && centered !== d) {
          var centroid = path.centroid(d);
          x = centroid[0];
          y = centroid[1];
          k = 4;
          centered = d;
          _this.openInfo(d.properties);
        } else {
          x = width / 2;
          y = height / 2;
          k = 1;
          centered = null;
          _this.closeInfo();
        }

        // Highlight the clicked province
        mapLayer.selectAll('path')
          .style('fill', function(d){
            return centered && d===centered ? '#D5708B' : fillFn(d);
        });

        // Zoom
        g.transition()
          .duration(750)
          .attr('transform', 'translate(' + width / 2 + ',' + height / 2 + ')scale(' + k + ')translate(' + -x + ',' + -y + ')');
        
      }

      const color = d3.scaleLinear()
        .domain([1, 20])
        .clamp(true)
        .range(['#08304b', '#08304b']);

      function mouseover(d){
        // Highlight hovered province
        d3.select(this).style('fill', '#1483ce');
        // d3.select(this).style('fill', '#004EA2');
        if(d) {
          _this.selectProvince(d.properties);
        }
      }

      function mouseout(d){
        _this.selectProvince(undefined);
        // Reset province color
        mapLayer.selectAll('path')
          .style('fill', (d) => {
            return centered && d===centered ? '#D5708B' : fillFn(d);
          });
      }

      // Get province name length
      function nameLength(d){
        const n = nameFn(d);
        return n ? n.length : 0;
      }

      // Get province name
      function nameFn(d){
        return d && d.properties ? d.properties.name : null;
      }

      function partyColor(d) {
        return _this.findPartyColor(d.properties.SGG_Code);
      }

      // Get province color
      function fillFn(d){
        // console.log(d, nameLength(d));
        // console.log(d.properties);

        const pcolor = partyColor(d);
        if(pcolor) {
          // console.log(pcolor);
          // d3.select(d).style('fill', pcolor);
          return pcolor;
        }

        return color(nameLength(d));
      }

      // Add background
      background
        .on('click', clicked);

      mapLayer
        .selectAll('path')
        .data(geojson.features)
        .enter().append('path') 
        .attr('d', path)
        .attr('vector-effect', 'non-scaling-stroke')
        .style('fill', fillFn)
        .on('mouseover', mouseover)
        .on('mouseout', mouseout)
        .on('click', clicked);

      const zoomed = () =>{
        mapLayer.attr('transform', d3.event.transform)
        // icons.attr('transform', d3.event.transform)
      }
      const zoom = d3.zoom().scaleExtent([1, 8]).on('zoom', zoomed)
      svg.call(zoom)
    }
  }
}

